.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navbar-2 {
  display: flex;
  gap: 101px;
  justify-content: flex-end;
  margin-right: 60px;
  line-height: 32px;
  font-size: 16px;
}

a {
  font-family: 'Noto Sans SC', sans-serif;
  color: aliceblue;
}

.navbar {
  display: grid;
  height: 73px;
  background: #358bcb;
  align-items: center;
  text-align: center;
  grid-template-columns: auto 3fr auto;
  grid-template-areas: "a a b" "a a b" "a a b";
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-margin {
  margin-top: 2.5rem;
  margin-bottom: 7rem;
}

.btn-1 {
  gap: 20px;
  flex-direction: row;
}

.btn-1 a {
  font-size: 21px;
}

.shadow-1-up {
  box-shadow: 0 0 0 2px #fff inset,
}